import mui from 'assets/js/mui.js';
class NewsData {
    constructor(url, option, type, callback) {
        this.url = url;
        this.type = type;
        this.init(option, callback);
    }
    init(op, cb) {
        mui.ajax(this.url, {
            data: op,
            dataType: 'json', //json
            type: this.type, //HTTP
            timeout: 1000, //10
            success: (data) => {
                if (data.ret == 1) {
                    cb(data);
                } else {

                }
            },
            error: function(xhr, type, errorThrown) {
                //
                if (type == 'timeout') {
                    plus.nativeUI.toast('');
                } else if (type == 'abort') {
                    plus.nativeUI.toast('');
                } else if (type == 'timeout') {
                    plus.nativeUI.toast('');
                }
                console.log(type);
            }
        });
    }
}
export default NewsData;


